import update from 'react-addons-update';
import {
  WAITING,
  SUCCESS,
  FAILURE,
} from './actions';

const initialState = {
  status: 'INIT',
  length: 0,
  error: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case WAITING:
      return update(state, {
        status: { $set: 'WAITING' },
        error: { $set: null },
      });
    case SUCCESS:
      return update(state, {
        status: { $set: 'SUCCESS' },
        length: { $set: action.length },
      });
    case FAILURE:
      return update(state, {
        status: { $set: 'FAILURE' },
        length: { $set: 0 },
        error: { $set: action.error },
      });
    default:
      return state;
  }
};



